export default {
  data() {
    return {
      Data: {
        gaopaibudaiA: "高牌不带A",
        gaopaidaiA: "高牌带A",
        duizi2_9: "对子2-9",
        duizi10_A: "对子10-A",
        shunzi: "顺子",
        jinhua: "金花",
        shunjin: "顺金",
        baozi: "豹子"
      },
      id: 0,
      keys: "",
      loading: false,
      list: []
    };
  },

  async created() {
    //获取数据
    let { data } = await this.$http.HallFunConfig.Getcard_type_general({
      key: "card_type_general.lua"
    });
    // console.log(data);
    this.id = data.data[0].id;
    this.keys = data.data[0].sys_key;
    let res = JSON.parse(data.data[0].sys_val);
    // console.log(res);
    Object.keys(res.card_type_general).forEach(item => {
      this.list.push({ title: item, val: res.card_type_general[item] });
    });
    // console.log(this.list);
    Object.keys(this.Data).forEach(item => {
      this.list.forEach(it => {
        if (item === it.title) {
          it.title = this.Data[item];
        }
      });
    });
  },

  methods: {
    async submit(type) {
      let newobj = {};
      Object.keys(this.Data).forEach(item => {
        this.list.forEach(it => {
          if (it.title === this.Data[item]) {
            newobj[item] = it.val;
          }
        });
      });

      if (type === 1) {
        let { data } = await this.$http.HallFunConfig.Putcard_type_general({
          keys: this.keys,
          values: JSON.stringify({ card_type_general: newobj }),
          id: this.id
        });
        // console.log(data);
        if (data.code === 1 && data.msg === "ok") {
          this.$message({
            type: "success",
            message: "保存成功!"
          });
        }else{
            this.$message({
              type: "warning",
              message: "保存失败!"
            });
          }
      } else if (type === 2) {
        this.loading = true;

        let { data } = await this.$http.HallFunConfig.Postcard_type_general({
          keys: this.keys,
          values: JSON.stringify({ card_type_general: newobj }),
          id: this.id
        });
        // console.log(data);
        if (data.code === 1 && data.msg === "ok") {
          this.loading = false;
          this.$message({
            type: "success",
            message: "发送服务器配置成功!"
          });
        }else {
              this.loading = false;
              this.$message({
                type: "warning",
                message: "发送服务器配置失败!"
              });
            }
      }
    }
  }
};



